import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";

import {
  Node,
  Avatar,
  Tagline,
  Vote,
  Title,
  Container,
  NodeBody,
  VotesCount,
  I,
  CommentsCount,
  CommentIcon
} from "./style";

class Posts extends Component {
  componentDidMount() {
    fetch("https://api.producthunt.com/v2/api/graphql", {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: "Bearer f20AQz3Ap7trjQyQESkDK655LXGSYgTDd0FElcccaJA"
      },
      body: JSON.stringify({
        query: `{
                posts {
                  edges {
                    node {
                      id
                      name
                      description 
                      votesCount
                      commentsCount
                      website
                      tagline
                      thumbnail {
                        url
                      }
                      media {
                        videoUrl
                        type
                        url
                      }
                    }
                  }
                }
              }`,
        variables: null
      })
    })
      .then(response => response.json())
      .then(data => this.props.addNewData(data.data.posts.edges));
    this.props.resetCounter();
  }
  render() {
    const { data } = this.props;
    return (
      <Container>
        {data.length === 0 && "loading..."}
        {data.map(item => (
          <Link
            to={`/${item.node.id}`}
            key={item.node.id}
            style={{ textDecoration: "none" }}
          >
            <Node>
              <Avatar src={item.node.thumbnail.url} />
              <NodeBody>
                <Title>{item.node.name}</Title>
                <Tagline>{item.node.tagline}</Tagline>
                <CommentsCount>
                  <CommentIcon className="fas fa-comment"></CommentIcon>
                  {item.node.commentsCount}
                </CommentsCount>
              </NodeBody>
              <Vote>
                <VotesCount>
                  <I className="fas fa-caret-up" />
                  {item.node.votesCount}
                </VotesCount>
              </Vote>
            </Node>
          </Link>
        ))}
      </Container>
    );
  }
}
export default withRouter(Posts);
